































import graceLoading from "../../../../graceUI/components/graceLoading.vue"
import {
	mapState
} from 'vuex'
export default {
	components: {
		graceLoading
	},
	data() {
		return {
			marketId: 0,
			//分类信息
			categories: [{
					cateid: 0,
					name: "待处理"
				},
				{
					cateid: 1,
					name: "待付款"
				},
				{
					cateid: 2,
					name: "已处理"
				},
				{
					cateid: 3,
					name: "已退款"
				}
			],
			// 当前选择的分类
			cateCurrentIndex: 0,
			// 内容实例，内容区域您根据项目情况设计即可，覆盖模式
			orderList: [],
			loading: {
				show: false,
				nextPages: 2,
				totalPages: 2,
				type: 0,
				text: ['加载更多', 'loading ......', '已加载全部']
			}
		};
	},
	computed: mapState(['user']),
	onLoad() {
		const market = uni.getStorageSync('market');
		if (market.isMarket) {
			this.marketId = market.marketId;
			uni.startPullDownRefresh();
		} else {
			uni.showToast({
				title: '系统错误',
				mask: false,
				duration: 1500
			});
			uni.navigateBack();
		}
	},
	onPullDownRefresh() {
		this.getList(1, this.categories[this.cateCurrentIndex].name)
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//监听触底加载事件
	onReachBottom: function() {
		this.loading.show = true;
		//判断当前是否正在加载
		if (this.loading.type === 1) {
			return;
		}
		//判断是否是最后一页
		if (this.loading.nextPages > this.loading.totalPages) {
			this.loading.type = 2; //显示已加载全部
			return;
		}
		this.loading.type = 1; //显示加载中
		uni.request({
			url: this.GLOBAL.serverSrc + 'market/management/order',
			method: 'POST',
			data: {
				phone: this.user.phone,
				token: this.user.token,
				market_id: this.marketId,
				page: this.loading.nextPages,
				type: this.categories[this.cateCurrentIndex].name
			},
			success: res => {
				if (res.data.status === 200) {
					this.orderList = this.orderList.concat(res.data.orderList);
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					});
				}
			},
			fail: (e) => {
				this.GLOBAL.requestFail(e);
			},complete: () => {
				this.loading.type = 0;
				this.loading.nextPages++;
			}
		});
	},
	methods: {
		getList: function(page, type) {
			uni.showLoading({
				title:'加载中...'
			})
			uni.request({
				url: this.GLOBAL.serverSrc + 'market/management/order',
				method: 'POST',
				data: {
					phone: this.user.phone,
					token: this.user.token,
					market_id: this.marketId,
					page: page,
					type: type
				},
				success: res => {
					if (res.data.status === 200) {
						this.orderList = res.data.orderList;
						this.loading.totalPages = res.data.totalPages;
						if(this.loading.totalPages > 1 && this.orderList.length > 0){
							this.loading.show = true;
						}else{
							this.loading.show = false;
						}
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						});
					}
				},
				fail: (e) => {
					this.GLOBAL.requestFail(e);
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		tabChange: function(e) {
			// 选中的索引
			var index = e.currentTarget.dataset.index;
			this.cateCurrentIndex = index;
			// 动态替换内容
			this.loading.nextPages = 2;
			this.getList(1, this.categories[index].name);
		}
	}
}
